import React, { useState } from 'react';
import './App.css';
import Visualizer from './Visualizer';
import ProgressBar from './ProgressBar';
import Sidebar from './Sidebar';

function App() {
  const [loading, setLoading] = useState(true);
  const [progress, setProgress] = useState(0);
  const [filterNoText, setFilterNoText] = useState(false);
  const [useLAB, setUseLAB] = useState(false);  // Neuer Zustand fr Koordinatentyp

  const toggleFilter = () => {
    setFilterNoText(!filterNoText);
  };

  const toggleCoordinateType = () => {
    setUseLAB(!useLAB);  // Umschalten zwischen x, y und L, A, B Koordinaten
  };

  return (
    <div className="App">
      {loading && <ProgressBar progress={progress} />}
      <Visualizer setLoading={setLoading} setProgress={setProgress} filterNoText={filterNoText} useLAB={useLAB} />
      <Sidebar onToggleFilter={toggleFilter} onToggleCoordinateType={toggleCoordinateType} />
    </div>
  );
}

export default App;