// ProgressBar.js
import React from 'react';

function ProgressBar({ progress }) {
  const containerStyles = {
    height: 20,
    width: '50%',  // Setzt die Breite der Progress Bar auf 50% der Gesamtbreite
    backgroundColor: "grey",
    borderRadius: 50,
    position: 'fixed',  // Fixiert die Position auf dem Bildschirm
    top: '50%',  // Vertikal zentriert
    left: '25%',  // Horizontale Positionierung, um das Zentrum zu erreichen (100% - 50%)/2 = 25%
    transform: 'translateY(-50%)',  // Verschiebt die Progress Bar um die Hlfte ihrer Hhe nach oben, um genau zu zentrieren
    zIndex: 1000,  // Stellt sicher, dass die Progress Bar ber anderen Elementen liegt
  };

  const fillerStyles = {
    height: '100%',
    width: `${progress}%`,
    backgroundColor: progress > 50 ? 'green' : 'white',  // Grn, wenn Fortschritt ber 50%, sonst Rot
    borderRadius: 'inherit',
    textAlign: 'right',
    transition: 'width 0.5s ease-in-out'
  };

  const labelStyles = {
    padding: 5,
    color: 'black',
    fontWeight: 'bold'
  };

  return (
    <div style={containerStyles}>
      <div style={fillerStyles}>
        <span style={labelStyles}>{`${progress}%`}</span>
      </div>
    </div>
  );
}

export default ProgressBar;