// Visualizer.js
import React, { useState } from 'react';
import { Canvas, useLoader } from '@react-three/fiber';
import { Plane, MapControls } from '@react-three/drei';
import * as THREE from 'three';
import useDataLoader from './useDataLoader';

const Visualizer = ({ setLoading, setProgress, filterNoText, useLAB }) => {
    const data = useDataLoader(useLAB, setLoading);

    return (
        <Canvas 
            frameloop="demand" 
            orthographic 
            camera={{ position: [0, 0, 50], zoom: 10, up: [0, 0, 1], far: 10000 }}
            style={{ width: '100vw', height: '100vh', backgroundColor: 'black' }}
        >
            <ambientLight intensity={0.5} />
            <pointLight position={[10, 10, 10]} />
            {data.map((d, index) => (
                <ImageThumbnail
                    key={index}
                    position={d.coordinates}
                    width={d.width}
                    height={d.height}
                    imageUrl={`/Images_thumb/${d.Image}`}
                    opacity={filterNoText && d.has_text === 'No' ? 0.3 : 1}
                />
            ))}
            <MapControls />
        </Canvas>
    );
};

const ImageThumbnail = ({ position, imageUrl, width, height, opacity }) => {
    const texture = useLoader(THREE.TextureLoader, imageUrl);

    return (
        <Plane args={[width, height]} position={position} rotation={[0, 0, -Math.PI / 2]}>
            <meshBasicMaterial attach="material" map={texture} toneMapped={false} opacity={opacity} transparent={true} />
        </Plane>
    );
};

export default Visualizer;