// useDataLoader.js
import { useState, useEffect } from 'react';
import Papa from 'papaparse';

const useDataLoader = (useLAB, setLoading) => {
    const [data, setData] = useState([]);

    useEffect(() => {
        fetch('/merged_images.csv')
            .then(response => response.text())
            .then(csvString => {
                Papa.parse(csvString, {
                    header: true,
                    dynamicTyping: true,
                    skipEmptyLines: true,
                    complete: (results) => {
                        const validData = results.data.filter(d => d.Image && (d.x !== undefined && d.y !== undefined));
                        const xyCenter = calculateCenter(validData, 'x', 'y');
                        const labCenter = calculateCenter(validData, 'L', 'A');
                        const newOffset = calculateOffset(xyCenter, labCenter);

                        setData(validData.map(d => ({
                            ...d,
                            width: 1,
                            height: 1,
                            opacity: d.has_text === 'No' ? 0.3 : 1,
                            coordinates: useLAB ? [d.L - newOffset[1], d.A - newOffset[1], 0] : [d.x - newOffset[0], d.y - newOffset[1], 0]
                        })));
                        loadImageSizes(validData);
                    }
                });
            });
    }, [useLAB]);

    const calculateCenter = (data, xKey, yKey) => {
        const xSum = data.reduce((sum, d) => sum + d[xKey], 0);
        const ySum = data.reduce((sum, d) => sum + d[yKey], 0);
        return [xSum / data.length, ySum / data.length];
    };

    const calculateOffset = (center1, center2) => {
        return [center1[0] - center2[0], center1[1] - center2[1]];
    };

    const loadImageSizes = (data) => {
        const imagePromises = data.map((item, index) => {
            return new Promise(resolve => {
                const img = new Image();
                img.onload = () => {
                    const width = img.naturalWidth;
                    const height = img.naturalHeight;
                    const aspectRatio = width / height;
                    resolve({ ...item, width: aspectRatio, height: 1, opacity: item.opacity, coordinates: item.coordinates });
                };
                img.src = `/Images_thumb/${item.Image}`;
            });
        });

        Promise.all(imagePromises).then(completedData => {
            setData(completedData);
            setLoading(false); // Set loading to false when all images are loaded
        });
    };

    return data;
};

export default useDataLoader;